import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";
// import { Redirect } from "react-router-dom";

import UpdateForm from "./updateForm";

import { Tabs } from "antd";

import notification from "../../../../components/notification";
import LayoutWrapper from "../../../../components/utility/layoutWrapper";
import PageHeader from "../../../../components/utility/pageHeader";
import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";
import { Box } from "../../../App/app.style";
import actions from "../../../../redux/supplier/courier/actions";


const TabPane = Tabs.TabPane;
const redirectTo = "/dashboard/setup/couriers";

class UpdateCourier extends Component {
  state = {
    id_courier: null,
    url: null,
  }
  componentWillMount = () => {
    this.setState({
      id_courier: this.props.match.params,
      url: this.props.match.url,
    })
  }

  render() {
    const courier = this.props.courier.courier ? this.props.courier.courier : []

    //Display error notif
    if (this.props.courier.error) {
      notification("error", this.props.courier.error);
      this.props.clearError();
    }

    // Display success notif
    if (this.props.courier.success) {
      notification("success", this.props.courier.success);
      this.props.clearSuccess();
    }
    //console.log(this.props.match);
    return (
      <LayoutWrapper>
        <Box>
          <PageHeader
            title={<IntlMessages id="couriers.update.title" />}
            marginBottom={true}
          >
            <Button
              onClick={() => this.props.history.replace(redirectTo)}
              style={{ marginLeft: "5px" }}
            >
              <IntlMessages id="button.back" />
            </Button>
          </PageHeader>
          <UpdateForm
            auth={this.props.auth}
            id_courier={this.state.id_courier}
            courier={this.props.courier}
          />
        </Box>
      </LayoutWrapper>
    );
  }
}

const mapStateToProps = state => {
  return { auth: state.auth, courier: state.courier };
};

export default compose(
  connect(
    mapStateToProps,
    actions
  )
)(UpdateCourier);
