const actions = {
  getCouriers: (page, search, filter) => ({
    type: "GET_COURIERS_REQUEST",
    payload: {
      page: page,
      search: search,
      filter: filter,
    }
  }),
  createCourier: () => ({
    type: "CREATE_COURIER_REQUEST",
  }),

  storeCourier: data => ({
    type: "STORE_COURIER_REQUEST",
    payload: { body: data }
  }),

  editCourier: id_courier => ({
    type: "EDIT_COURIER_REQUEST",
    payload: { id_courier: id_courier }
  }),

  updateCourier: (id_courier, data) => ({
    type: "UPDATE_COURIER_REQUEST",
    payload: {
      id_courier: id_courier,
      body: data
    }
  }),
  showCourier: id_courier => ({
    type: "SHOW_COURIER_REQUEST",
    payload: { id_courier: id_courier }
  }),
  deactivateCourier: id_courier => ({
    type: "DEACTIVATE_COURIER_REQUEST",
    payload: { id_courier }
  }),
  activateCourier: id_courier => ({
    type: "ACTIVATE_COURIER_REQUEST",
    payload: { id_courier }
  }),
  clearSuccess: () => ({
    type: "CLEAR_SUCCESS"
  }),
  clearError: () => ({
    type: "CLEAR_ERROR"
  }),
  clearErrorField: () => ({
    type: "CLEAR_ERROR_FIELD"
  })
};

export default actions;
