import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";
import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";
import { Form, Input, InputNumber, Row, Col, Alert, Select, Radio, Spin } from "antd";
import actions from "../../../../redux/supplier/courier/actions";

const { Option } = Select;
const { TextArea } = Input;
const InputGroup = Input.Group;

class CourierUpdateForm extends Component {

  componentDidMount() {
    const { editCourier } = this.props;
    const { id } = this.props.id_courier;
    editCourier(id);
  }

  componentDidUpdate() {
    // Display error notif
    // if (this.props.courier.error) {
    //   notification("error", this.props.courier.error);
    //   this.props.clearError();
    // }
    // data.is_taxable!==undefined 
    //   ? this.setState({is_taxable: 1})
    //   : this.setState({is_taxable: null})

    if (this.props.courier.success) {
      // notification("success", this.props.courier.success);
      // this.props.clearSuccess();
      const { editCourier } = this.props;
      const { id } = this.props.id_courier;
      editCourier(id);
    }
  }


  handleSubmit = e => {
    //console.log("Masuk Pak")
    e.preventDefault();
    this.props.form.validateFieldsAndScroll((err, value) => {
      if (!err) {
        const { updateCourier } = this.props;
        const { id } = this.props.id_courier;
        updateCourier(id, value);
      }
    });
  };


  render() {
    // console.log(this.props.courier);
    const { getFieldDecorator, getFieldError } = this.props.form;
    const { courier } = this.props;
    const { loading, loadingSubmit } = courier;
    const data = courier.courier ? courier.courier : [];

    const used = data.is_used;
    const error = courier.errorField ? courier.errorField : [];
    // console.log(data);

    // {data.is_taxable!==undefined 
    //   ? this.setState({is_taxable: 1})
    //   : this.setState({is_taxable: null})
    // }

    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      }
    };
    const formTextArea = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      }
    };
    const tailFormItemLayout = {
      wrapperCol: {
        xs: {
          span: 24,
          offset: 0
        },
        sm: {
          span: 24,
          offset: 0
        }
      }
    };

    const radioGroupStyle = {
      display: "flex",
      width: "100%"
    }

    const radioButtonStyle = {
      flex: "1",
      textAlign: "center"
    }

    return (
      <div>
        {this.props.courier.loadingEdit ? (
          <div className="spinStyle">
            <Spin size="large" />
          </div>
        ) : (

            <Form layout="vertical" onSubmit={this.handleSubmit}>
              {used ? (
                <Row style={{ marginBottom: "20px" }}>
                  <Col span={24}>
                    <Alert
                      message={<IntlMessages id="courier.help" />}
                      type="warning"
                      showIcon
                    />
                  </Col>
                </Row>
              ) : (
                  ""
                )}
              {/* <Divider /> */}
              <div className="isoDetailDataFlex" style={{ padding: "0" }}>
                <div className="isoColumn-2 isoFormGroupComp" style={{ padding: "0 15px 0 0" }}>

                  <Form.Item
                    {...formItemLayout}
                    label={<IntlMessages id="couriers.add.name" />}
                    validateStatus={
                      error.courier_name || getFieldError("courier_name")
                        ? "error"
                        : null
                    }
                    help={
                      error.courier_name
                        ? error.courier_name
                        : getFieldError("courier_name")
                    }
                  >
                    {getFieldDecorator("courier_name", {
                      initialValue: data.courier_name,
                      rules: [
                        {
                          required: true,
                          message: <IntlMessages id="required" />
                        },
                        {
                          max: 30,
                          message: <IntlMessages id="maxLength30" />
                        }
                      ]
                    })(
                      <Input
                        disabled={used ? true : false}
                        style={{ width: "100%" }}
                      />
                    )}
                  </Form.Item>

                  <Form.Item
                    {...formItemLayout}
                    label={<IntlMessages id="locations.add.description" />}
                  >
                    {getFieldDecorator("description", {
                      initialValue: data.description,
                      rules: [
                        {
                          required: true,
                          message: <IntlMessages id="required" />
                        },
                        {
                          max: 50,
                          message: <IntlMessages id="maxLength50" />
                        }
                      ]
                    })(<Input />)}
                  </Form.Item>
                  <InputGroup compact>
                    <Col span={12}>
                      <Form.Item
                        {...formItemLayout}
                        label={<IntlMessages id="isInternal" />}
                      >
                        {getFieldDecorator("is_internal", {
                          initialValue: data.is_internal,
                          rules: [
                            {
                              required: true,
                              message: <IntlMessages id="required" />
                            }
                          ]
                        })(
                          <Radio.Group
                            buttonStyle="solid"
                            style={radioGroupStyle}
                          >
                            <Radio.Button style={radioButtonStyle} value={1}><IntlMessages id="Yes" /></Radio.Button>
                            <Radio.Button style={radioButtonStyle} value={0}><IntlMessages id="No" /></Radio.Button>
                          </Radio.Group>
                        )}
                      </Form.Item>
                    </Col>
                    <Col span={12}>
                      <Form.Item
                        {...formItemLayout}
                        label={<IntlMessages id="defaultShippingPrice" />}
                      >
                        {getFieldDecorator("default_shipping_price", {
                          initialValue: data.default_shipping_price,
                          //initialValue: 0, 
                          // rules: [
                          //   {
                          //     required: true,
                          //     message: <IntlMessages id="required" />
                          //   }
                          // ]
                        })(
                          <InputNumber
                            formatter={value =>
                              `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ",")
                            }
                            parser={value => value.replace(/\$\s?|(,*)/g, "")}
                            //min={0}
                            //disabled={used ? true : false}
                            style={{ width: "100%" }}
                          />
                        )}
                      </Form.Item>
                    </Col>
                  </InputGroup>


                  <Form.Item
                    {...formItemLayout}
                    label={<IntlMessages id="vehicleNo" />}
                  >
                    {getFieldDecorator("vehicle_no", {
                      initialValue: data.vehicle_no,
                      rules: [
                        {
                          max: 15,
                          message: <IntlMessages id="maxLength15" />
                        },
                      ]
                    })
                      (<Input
                      //autosize={{ minRows: 2 }} 
                      //disabled={used ? true : false} 
                      />
                      )
                    }
                  </Form.Item>


                  <Form.Item
                    {...formItemLayout}
                    label={<IntlMessages id="couriers.filter.relatedEmployee" />}
                  >
                    {getFieldDecorator("id_related_employee", {
                      initialValue: data.id_related_employee,
                      // rules: [
                      //   {
                      //     required: true,
                      //     message: <IntlMessages id="required" />
                      //   }
                      // ]
                    })(
                      <Select
                        loading={this.props.courier.loadingFilterEmployees}
                        showSearch
                        optionFilterProp="children"
                      //disabled={used ? true : false}
                      >
                        {this.props.courier.employees.map((data, key) => (
                          <Option key={key} value={data.id_employee}>
                            {data.employee_code + " (" + data.first_name + (data.last_name ? " " + data.last_name + ")" : ")")}
                          </Option>
                        ))}
                      </Select>
                    )}
                  </Form.Item>

                </div>
                <div className="isoColumn-2 isoFormGroupComp" style={{ padding: "0 0 0 15px" }}>

                  <Form.Item
                    {...formItemLayout}
                    label={<IntlMessages id="phone1" />}
                  >
                    {getFieldDecorator("phone1", {
                      initialValue: data.phone1,
                      rules: [
                        // {
                        //   required: true,
                        //   message: <IntlMessages id="required" />
                        // },
                        {
                          max: 15,
                          message: <IntlMessages id="maxLength15" />
                        },
                        {
                          pattern: "^[0-9]*$",
                          message: <IntlMessages id="invalidPhone" />
                        },
                      ]
                    })
                      (<Input
                      //autosize={{ minRows: 2 }} 
                      //disabled={used ? true : false} 
                      />
                      )
                    }
                  </Form.Item>

                  <Form.Item
                    {...formItemLayout}
                    label={<IntlMessages id="phone2" />}
                  >
                    {getFieldDecorator("phone2", {
                      initialValue: data.phone2,
                      rules: [
                        {
                          max: 15,
                          message: <IntlMessages id="maxLength15" />
                        },
                        {
                          pattern: "^[0-9]*$",
                          message: <IntlMessages id="invalidPhone" />
                        }
                      ]
                    })
                      (<Input
                      //autosize={{ minRows: 2 }} 
                      //disabled={used ? true : false} 
                      />
                      )
                    }
                  </Form.Item>

                  <Form.Item
                    {...formItemLayout}
                    label={<IntlMessages id="email" />}
                  >
                    {getFieldDecorator("email", {
                      initialValue: data.email,
                      rules: [
                        {
                          type: "email",
                          message: <IntlMessages id="invalidEmail" />
                        }
                      ]
                    })(<Input />)}
                  </Form.Item>

                  <Form.Item
                    {...formTextArea}
                    label={<IntlMessages id="address" />}
                  >
                    {getFieldDecorator("address", {
                      initialValue: data.address,
                    })
                      (<TextArea
                        autosize={{ minRows: 5 }}
                      //disabled={used ? true : false} 
                      />
                      )
                    }
                  </Form.Item>
                </div>
              </div>

              <Row>
                <Col>
                  <Form.Item {...tailFormItemLayout}>
                    <Button
                      type="primary"
                      htmlType="submit"
                      style={{ marginTop: "20px", marginLeft: "50px" }}
                      loading={loading || loadingSubmit}
                    >
                      <IntlMessages id="button.submit" />
                    </Button>
                  </Form.Item>
                </Col>
              </Row>


            </Form>
          )}
      </div>
    );
  }
}

const WrappedCourierUpdateForm = Form.create({
  name: "update_courier"
})(CourierUpdateForm);

//export default WrappedCourierUpdateForm;

const mapStateToProps = state => {
  return { courier: state.courier };
};

export default compose(
  connect(
    mapStateToProps,
    actions
  )
)(WrappedCourierUpdateForm);
